import React, {Component} from 'react'
import BaseContent from '@/components/BaseContent'

import {connect} from 'dva'
import {getUrlParams} from '@/utils/utils'

let mapState = store=>{
  return {...store.addExam, ...store.main}
}

@connect(mapState)
class ExamDetail extends Component{
  state = {}
  
  componentDidMount () {
      const id = getUrlParams(window.location.href).id
      console.log(id)
      this.props.dispatch({type: 'addExam/getExamDetail', exam_id: id})
  }
  render () {
    return (
      <React.Fragment>
        <BaseContent>
        <p>考试时间：1小时30分钟		监考人：刘于       开始考试时间：2018.9.10  10:00  阅卷人：刘于</p>
        </BaseContent>

        <BaseContent>
        <p>考试时间：1小时30分钟		监考人：刘于       开始考试时间：2018.9.10  10:00  阅卷人：刘于</p>
        </BaseContent>
      </React.Fragment>
    )
  }
}
export default ExamDetail;